import React, { useState, useEffect } from 'react';
import axios from 'axios';

import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import List from '@mui/material/List';
import styled from "styled-components";
import { Link } from 'react-router-dom';
import DetailList from './DetailList';
import { LocalShipping } from '@material-ui/icons';



const Div = styled.div`
@import url("https://fonts.googleapis.com/css2?family=Jua&display=swap");

    display:flex;
    flex-direction: row;


    .phoneImage{
      margin: 10px
    }
    .infomation{
      margin-top :10px;
      font-size: 15px;
      font-family: "Jua", sans-serif
    }
    `


function SideList() {
  const [shop, setShop] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [detail, setDetail] = useState(false);

  const [idGet,setIdGet] = useState('hi');

  useEffect(() => {
    console.log('axios1')
    const fetchShops = async () => {
      try {
        setError(null);
        setShop(null);
        setLoading(true);
        const response = await axios.get(
          'http://sulrae.com/api/shop/'
        );
        setShop(response.data);
        console.log(response.data)
      } catch (e) {
        setError(e);
      }
      setLoading(false);
    };

    fetchShops();
  },[]);

  const ButtonClick = () =>{
    setDetail(true)
  }


  if (loading) return <div>Ïû†ÏãúÎßå Í∏∞Îã§Î¶¨Í∞úüê∂</div>;
  if (error) return <div>Error</div>;
  if (!shop) return <div>Ìï¥ÎãπÏßÄÏó≠ÏóêÎäî ÏïÑÏßÅ Îì±Î°ùÎêú Î∞òÎ†§ÎèôÎ¨º ÌóàÏö©Ïπ¥ÌéòÍ∞Ä ÏóÜÎã§Í∞úüê∂ </div>;
  if (detail) return <DetailList/>


  return (
      <>
        {shop.map(shop => (
          <ListItem button onClick={ButtonClick} key={shop.id}>
            
                  <Div>
                    <img className="phoneImage" alt={shop.id} src={shop.image1} width="170px"  height="140px" />
                    <div className="infomation">
                      <b>{shop.name}</b> ‚≠ê{shop.score}.0 <br/>
                      {shop.address} <br/>
                      {shop.tel}
                      {/* {shop.shop_id} */}

                    </div>
                  </Div> 


            </ListItem>

        ))}




      </>
  );
}

export default SideList;